import React from 'react';
import { Link } from 'react-router-dom';
import './App.css';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';

export function HomePage() {
  return (
    <div className="website-container">
      <div className="content-container">
        <p className="intro-text">
          <h2 className="name">Marwan Alshaker ðŸ‘‹</h2>
          <p className="tagline">
            I'm a self-taught JavaScript engineer with two years of experience.
            I learn through building products and solving problems. I can now
            build high-grade, production-ready React apps. I'm also starting to
            be an OSS contributor to WordPress.
          </p>
        </p>
        <div className="button-container">
          <Link
            className="home-page-button about-button"
            to="/about"
            style={{
              textDecoration: 'none',
            }}
          >
            About me
          </Link>
          <button
            className="home-page-button follow-button"
            style={{
              textDecoration: 'none',
            }}
            onClick={() => window.open('https://twitter.com/m_alshaker/')}
          >
            Follow me on Twitter
          </button>
        </div>
      </div>
      <div className="projects-container">
        <div className="other">
          <p className="other-projects-title">Projects</p>
          <p className="other-projects-text">
            I have done some project, my favorites:
          </p>
          <div className="card-container">
            <ul>
              <li>
                <Card
                  variant="outlined"
                  style={{
                    margin: '10px 0',
                    backgroundColor: 'rgba(151, 151, 153, 0.1)',
                    padding: 15,
                  }}
                  className="card"
                >
                  <CardContent>
                    <div className="description">
                      <div className="app-info">
                        <h3 className="title">Imitategram</h3>
                        <p className="card-text">An instagram replica</p>
                        <a
                          className="blog"
                          href="https://dev.to/alshakerm/building-an-instagram-replica-from-scratch-39fb"
                          target="_blank"
                          rel="noreferrer"
                        >
                          A detailed blog about the creation of Imitategram
                        </a>
                      </div>
                      <div className="tech">
                        <h3 className="tech-header">Tech used</h3>
                        <ul className="tech-list">
                          <li>React</li>
                          <li>
                            Redux Saga{' '}
                            <a
                              className="wordpressLink"
                              href="https://developer.wordpress.org/block-editor/reference-guides/packages/packages-data/"
                            >
                              (WordPress data stores).
                            </a>
                          </li>
                          <li>Next.js</li>
                        </ul>
                        <p className="blog-text">
                          For more details please visit the blog
                        </p>
                      </div>
                      <div className="links">
                        <a
                          className="repo"
                          href="https://github.com/alshakerM/my-imitategram"
                          target="_blank"
                          rel="noreferrer"
                        >
                          GitHub Repository
                        </a>
                      </div>
                    </div>
                  </CardContent>
                  <CardActions>
                    <Button
                      id="try"
                      href="https://imitategram.vercel.app/"
                      target="_blank"
                    >
                      Try it
                    </Button>
                  </CardActions>
                </Card>
              </li>
              <li>
                <Card
                  style={{
                    margin: '10px 0',
                    backgroundColor: 'rgba(151, 151, 153, 0.1)',
                    padding: 15,
                  }}
                  className="card"
                >
                  <CardContent>
                    <div className="description">
                      <div className="app-info">
                        <h3 className="title">Mente</h3>
                        <p className="card-text">Meditation app</p>
                      </div>
                      <div className="tech">
                        <h3 className="tech-header">Tech used</h3>
                        <ul className="tech-list">
                          <li>React</li>
                          <li>TypeScript</li>
                          <li>Next.js</li>
                        </ul>
                      </div>
                      <div className="links">
                        <a
                          className="repo"
                          href="https://github.com/alshakerM/Mente"
                          target="_blank"
                          rel="noreferrer"
                        >
                          GitHub Repository
                        </a>
                      </div>
                    </div>
                  </CardContent>
                  <CardActions>
                    <Button
                      id="try"
                      href="http://mente.vercel.app/"
                      target="_blank"
                    >
                      Try it
                    </Button>
                  </CardActions>
                </Card>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
}
